@charset "utf-8";

* {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}

/* 변수로 색상 지정 */

:root {
  --basecColor: white;
  /* 주조색(배경색) - 약 70% */
  --dominantColor: black;
  /* 보조색 - 약 20% 사용 */
  --accentColor: #0df;
  /* 포인트색 - 약 10% 사용 */
}

h1 {
  color: var(--accentColor);
  background-color: var(--dominantColor);
  line-height: 2;
  text-align: center;
}

section {
  margin-top: 3em;
}

h2 {
  text-align: center;
  font-size: 1em;
  color: var(--accentColor);
  height: 2em;
}

article {
  padding: 1em;
}

h3 {
  margin-bottom: 1em;
}

article ul, article ol {
  padding-left: 2em;
}

section>article>ol>li {
  font-weight: bold;
  font-size: 18px;
}

section>article>ol>li>ul>li {
  font-weight: normal;
  font-size: 16px;
  list-style: disc;
}

section>article>ol>li>ol>li {
  font-weight: normal;
  font-size: 16px;
}

section>article>ol>li>ol>li>ul {
  list-style: none;
}

section>article>ol>li>ol>li>ul>li {
  font-weight: bold;
  font-size: 18px;
  margin-top: 10px;
}

section>article>ol>li>ol>li>ul>li>ol>li {
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
}

section>article>ol>li p {
  font-weight: normal;
  font-size: 16px;
}

table {
  border-collapse: collapse;
  border-spacing: none;
  width: 100%;
}

th, td {
  border: 1px solid var(--dominantColor);
  padding: 0.5em;
}

thead th {
  color: var(--basecColor);
  background-color: sienna;
}

table>caption {
  text-align: left;
}

tbody th {
  background-color: chocolate;
}

.grey {
  color: #ddd;
}

.sky {
  color: #0df;
  font-weight: bold;
}

.br {
  font-size: 14px;
}